.gradient {
    position: relative;
    border-radius: 0 0 12px 12px;
    background: $gr-supersoft;

    &::after {
        content: '';
        height: 10px;
        border-radius: 0 0 12px 12px;
        position: absolute;
        bottom: -10px;
        left: 20px;
        right: 20px;
        background-color: #CACADB;;
    }

    &::before {
        content: '';
        height: 10px;
        border-radius: 0 0 12px 12px;
        position: absolute;
        bottom: -20px;
        left: 40px;
        right: 40px;
        background-color: #E7E7F2;
    }

    &__simvol {
        background: url('../img/symbol.svg') no-repeat center top;
    }
}

@media (min-width: 678px) {

    .gradient{

        &__simvol {
            background-size: cover;
        }
    }
}
@media (min-width: 934px) {

    .gradient{

        &__simvol {
            background-size: contain;
        }
    }
}
